import React, {useState, useEffect} from 'react';
import Items from '../components/Items';
import store from "../store/store";
import {addToStore} from "../store/actions";
import "../css/home.css";

function Home() {
    const [value, setValue] = useState("")
    const [isLoading, setIsLoading] = useState(false)
    const [data, setData] = useState([])
    const [param, setparam] = useState("")
    const [btn, setBtn] = useState("Search")

    useEffect(()=> {
        setData(store.getState())
    }, [])

    const APP_ID = "0e8691f1"
    const APP_KEY = "4c329190f0a88cd9bf1d3d6c48b17901"

    const url = `https://api.edamam.com/search?q=${value}&app_id=${APP_ID}&app_key=${APP_KEY}&from=0&to=12`


    const changeState = (state) => {
        if(state){
            setBtn("Searching")
            setIsLoading(true)
        }
        else{
            setBtn("Search")
            setIsLoading(false)
        }
    }

    const search = async () =>{
        changeState(true)

        const request = await fetch(url)
        const response = await request.json()
        setValue('')

        if(response){
            changeState(false)
            console.log(response)

            store.dispatch(addToStore(response.hits))
            setData(response.hits)
            setparam(response.q)

        }
    }

    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12 col-md-10 col-lg-8 mt-5 mx-auto">
                    <div className="input-group align-items-center">
                        <input placeholder="Search recipe" className="form-control box-none" onChange={(e)=> setValue(e.target.value)}  value={value} />
                        <button onClick={()=> search().catch(err => changeState(false))} disabled={value?.length < 2} className="btn btn-danger btn-costum">{btn}</button>
                    </div>
                </div>
            </div>
            
            {
                isLoading ? <h4 className="text-secondary text-center my-3">Loading...</h4> : ""
            }

            {
                param ? 
                    <div className="mt-4">
                        <h3 className="text-secondary text-capitalize">{param}</h3>
                        <hr className="my-2"/>
                    </div>
                :   
                    ""
            }
            <div className="row mt-4">
                {
                    (data?.length)?
                        data.map((item, index)=>(
                            <Items 
                                key={index}
                                label={item.recipe.label}
                                id={index}
                                image={item.recipe.image}
                                calories={item.recipe.calories}
                            />
                        ))
                    :
                        ""
                }
            </div>
        </div>
    );
}

export default Home;