import React, {useState, useEffect} from 'react';
import store from "../store/store"
import "../css/single.css"

function Singles(props) {
    const {id} = props.match.params

    const [recipeItems, setRecipeItems] = useState([])

    useEffect(() => {
        getItems()
    }, [id])


    const getItems = () =>{
        setRecipeItems(store.getState().filter((a, i) => i == id)[0].recipe)
    }

    const {label, image, cautions, healthLabels, ingredients} = recipeItems

    const style = {
        line: {
            padding: "2px 0",
            borderRadius: "4px"
        }
    }

    return (
        <div className="container mt-5">
            <h3 className="my-2 text-center">{label}</h3>
            <hr className="bg-danger" style={style.line}/>

            <div className="row mt-3">
                <div className="col-sm-12 mb-8 col-md-4 mb-5 col-lg-4">
                    <div className="card shadow rounded">
                        <img className="card-img-top img-fluid" src={image} alt="" />
                        {
                            (cautions?.length) ?
                                <div>
                                    <h5 className="py-2 text-white px-2 bg-danger">Cautions</h5>
                                    <ul className="list-group">
                                        {
                                            cautions.map(item => (
                                                <li className="list-group-item border-0">{item}</li>
                                            ))
                                        }
                                    </ul>
                                </div>
                            :
                                ""
                        }

                        {
                            (healthLabels?.length) ?
                                <div>
                                    <h5 className="py-2 text-white px-2 bg-success">Health Labels</h5>
                                    <ul className="list-group">
                                        {
                                            healthLabels.map(item => (
                                                <li className="list-group-item border-0">{item}</li>
                                            ))
                                        }
                                    </ul>
                                </div>
                            :
                                ""
                        }
                    </div>
                </div>
                <div className="col-sm-12 col-md-8 col-lg-8">
                    <h4 className="mb-3">Ingredients</h4>

                    <table class="table w-100 table-striped shadow table-inverse">
                        <thead class="thead-inverse">
                            <tr>
                                <th>Image</th>
                                <th>Ingredient</th>
                                <th>Weight</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                (ingredients?.length)?
                                    ingredients.map(ingredient => (
                                        <tr>
                                            <td scope="row">
                                                <img className="img-thumbnail" src={ingredient.image} alt=""/>
                                            </td>
                                            <td>{ingredient.text}</td>
                                            <td>{(Number(ingredient.weight).toFixed(1) + " kg")}</td>
                                        </tr>
                                    ))
                                : 
                                    ""
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}

export default Singles;