import React from 'react';
import {Link} from 'react-router-dom';
import "../css/item.css";

const Items = (props) =>{
    const {label, image, calories, id} = props
    return (
        <div className="col-sm-12 col-md-6 col-lg-4 my-3">
            <Link to={("/"+ id)} className="shadow card bordered">
                <img className="card-img-top img-fluid" src={image} alt={('Image of ' + label)} />
                <div className="card-body">
                    <h5 className="card-title text-secondary">{label}</h5>
                    <p className="card-text text-secondary">Calories: {parseFloat(calories).toFixed(2)}</p>
                </div>
            </Link>
        </div>
    );
}

export default Items;